.hotbutton{--length: 50px;position:fixed;min-width:var(--length);min-height:var(--length);background-color:#353535;box-shadow:10px 5px 5px rgba(128,128,128,.765);top:90px;border-radius:.5em;right:10px;transition-duration:1s;color:#fff;font-style:oblique;display:flex;align-items:center;justify-content:center;flex-direction:column;flex-grow:1}.hotbutton .remaining{position:absolute;border-radius:1.5em;width:50px;height:23px;top:-10px;left:-15px;font-style:normal;background-color:#ff1f1f;display:flex;align-items:center;justify-content:center;font-weight:bold;overflow:hidden}.hotbutton .margin10{margin:10px}.hotbutton .createbuild{background-color:#02b802;font-size:1em;margin:6px 3px 3px 3px;padding:5px;color:#fff;font-weight:lighter;min-width:100px;height:5%;border-radius:.5em;text-align:center;display:flex;align-items:center;justify-content:center}.hotbutton .createbuild:hover{cursor:pointer}.hotbutton .builddetail{background-color:#008ee6;min-width:100px;height:5%;border-radius:.5em;margin:5px;padding:5px}.hotbutton .builddetail:hover{cursor:pointer}.hotbutton .smallitemlist{color:#fff;position:relative;background-color:#353434;border:5px solid #ebe8e8;border-radius:.5em;margin:3px;min-width:100px;height:5%;padding:10px;display:flex;align-items:center;justify-content:flex-start;flex-direction:row;font-size:1em;font-weight:600;text-align:center;animation:appear .2s;animation-fill-mode:both}.hotbutton .smallitemlist:hover{cursor:pointer;background-color:#5a5a5a}@keyframes appear{from{opacity:0}to{opacity:1}}.removeitem{width:15px;height:15px;margin:0px 5px 0px 0px;background-color:red;border:2px solid #d9d9d9;border-radius:.25em}.removeitem::before{content:"×";position:relative;top:-7.5px;font-size:15pt;color:#fff;transform:rotote(45deg)}.removeitem:hover{cursor:pointer}.plusbutton{width:15px;height:15px;margin:0px 10px 0px 0px;background-color:#108916;border:2px solid #d9d9d9;border-radius:.25em}.plusbutton::before{content:"+";position:relative;top:-7.5px;font-size:15pt;color:#fff}.plusbutton:hover{cursor:pointer}.item-show-wrapper{position:fixed;width:300px;top:10%;left:10px;background-color:#222;overflow:hidden;z-index:2}.exportbutton{position:relative;min-width:100px;margin:6px 3px 3px 3px;background-color:#73008a;font-weight:500;padding:5px;border-radius:.5em;font-size:17px}.exportbutton:hover{cursor:pointer}